import React from 'react';
import { Link } from 'react-router';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';

const Photo = React.createClass({

	renderTags(tag, i) {
		console.log(i);
		return (
			<span key={i} className="tag">
				{tag.name}
			</span>
		)
	},

	render(){

		return (
			<div className="list-post">
				<figure className="grid-figure">
					<div className="grid-photo-wrap">
						
							<img src="http://placekitten.com.s3.amazonaws.com/homepage-samples/408/287.jpg" alt={this.props.post.caption} className="grid-photo"/>
						
						<ReactCSSTransitionGroup
							transitionName="like"
							transitionEnterTimeout={1000}
							transitionLeaveTimeout={1000}
						>
							<span key={this.props.post.likes} className="likes-heart">
								{this.props.post.likes}
							</span>
						</ReactCSSTransitionGroup>
					</div>
					<Link to={`/view/${this.props.post.code}`}>
					<figcaption>
							{this.props.post.postHasTags.map(this.renderTags )}
							{this.props.post.body}

					</figcaption>
					</Link>
					<div className="control-buttons">
						<button onClick={this.props.increment.bind(null, this.props.i)} className="likes">&hearts;&nbsp;{this.props.post.likes} </button>
					</div>
				</figure>
				<div className="post-infos">
					<Link to={this.props.post.link}>{this.props.post.link}</Link><br/>
					<span  className="tag">
						{this.props.post.title}
					</span>
				</div>
			</div>
		)
	}
});

export default Photo;


